<template>
	<div class="product-btns">
		<div class="btns-box">
			<div class="item-btn" v-for="(item,index) in itemArr" :key="index" >
				<a href="#" class="i-btn" @click="itemClick(index)" :class="{btnaction:currentIndex==index}">{{item}}</a>
			</div>
		</div>
	</div>
</template>

<script>


export default {
  name: 'ProductBtns',
  components:{
  },
  data(){
	return{
		itemArr:['今日有变化','库存不足','库存过多'],
		currentIndex:-1
	}
  },
  methods:{
	itemClick(index){
		if(this.currentIndex!==index){
			this.currentIndex=index
		}else{
			this.currentIndex=-1
		}
		//点击了-事件总线
		this.$emit("BtnClick",this.currentIndex)
	}
  }
}
</script>

<style spoced>
.product-btns{
	padding:10px 0;
}
.btns-box{
	display:flex;
}
.item-btn{
	flex:1;
	text-align:center;
}
.i-btn{
	display:inline-block;
	line-height:25px;
	padding:0 20px;
	border-radius:12px;
	font-size:13px;
	background:#dadada;
	color:black;
}
.btnaction{
	background:orange;
	color:#fff;
}


</style>
