<template>
	<div class="category">
		<!--顶部菜单-->
		<nav-bar class="product-nav-bar">
			<div slot="center">
				{{title}}
			</div>
			<div slot="right">
			</div>
		</nav-bar>
		<product-btns @BtnClick="BtnClick"  class="product-btns nav2" ref="nav1" v-show="topIsShow"/>
				<!--滚动-->
		<scroll class="product-scroller"
				ref="scroller"
				@pullingup="loadMore"
				@bscroll="bscroll"
				:probe-type=3
		>
		<product-navs/>
		<product-btns @BtnClick="BtnClick"  class="product-btns" ref="nav2"/>
		<product-content :products-arr="productsArrs"/>
		</scroll>
		<div class="create-product" @click="createClick">+</div>

	</div>
</template>

<script>


import NavBar from 'components/common/navbar/NavBar'

import Scroll from 'components/common/scroll/Scroll'
import ProductNavs from './childComps/ProductNavs'
import ProductContent from './childComps/ProductContent'
import ProductBtns from './childComps/ProductBtns'
//获取商品axios
import {getGoodsDatas} from 'network/product'


export default {
  name: 'Product',
  components:{
	NavBar,
	Scroll,
	ProductBtns,
	ProductNavs,
	ProductContent
  },
  data(){
	return{
		title:'产品列表',
		banners:[],
		roleArr:[],
		productsArr:{
			allSelect:{
				list:[],
				page:0,
				isMore:true,
			},
			TodayChange:{
				list:[],
				page:0,
				isMore:true,
			},
			StockShort:{			//其实这里是从后台拉取
				list:[],		
				page:0,
				isMore:true,
			},
			StockMore:{
				list:[],
				page:0,
				isMore:true,
			},
		},
		//当前选中的状态
		currentType:'allSelect',			//默认全部
		topIsShow:false,					//是否显示头部菜单		
		kuCunMinObj:{						//库存类型
			HuaWei:{
				maxstock:300,					//限制
				minstock:10
			},
			Iphone:{
				maxstock:20,
				minstock:10
			}
		},
		info:'',
	}
  },
  methods:{
	createClick(){
		this.$router.push('/addgoods').catch(res=>{res})
	},
	BtnClick(index){
		switch(index){
			case -1:
				this.currentType='allSelect'
				break
			case 0:
				this.currentType='TodayChange'
				break
			case 1:
				this.currentType='StockShort'
				break
			case 2:
				this.currentType='StockMore'
				break
		}
		this.$refs.nav1.currentIndex=index
		this.$refs.nav2.currentIndex=index
	},
	//滚动监听
	bscroll(position){
		let y=-position.y
		//46需要求一个组件的高度
		this.topIsShow=y>46
	},
	//把指定数字stock<n的商品
	filterArrstock(){
		this.productsArr.StockShort.list=[]								//清空-库存不足
		this.productsArr.StockMore.list=[]								//清空-库存过多
		this.productsArr['allSelect'].list.forEach(obj=>{
			if(obj.stock<this.kuCunMinObj.HuaWei.minstock){
				this.productsArr.StockShort.list.push(obj)				//库存不足[]
			}else if(obj.stock>this.kuCunMinObj.HuaWei.maxstock){
				this.productsArr.StockMore.list.push(obj)				//库存过多[]
			}
		})
	},
	//获取商品
	getGoodsDatas(type,productname=""){
		let page=this.productsArr[type].page+1;
		//获取商品
		getGoodsDatas(type,page,productname).then(res=>{
			this.productsArr[type].list.push(...res);
			this.productsArr[type].page+=1
			//console.log(res)
			//如果没有数据了
			if(res.length<=0){
				//不在加载更多
				this.productsArr[type].isMore=false
			}
		})
	},
	//加载更多
	loadMore(){
		//防抖
		if(timer) clearTimeout(timer)
		let timer=setTimeout(()=>{
			this.getGoodsDatas(this.currentType)
			if(this.productsArr[this.currentType].isMore){
				this.$refs.scroller.finishPullUp()
			}
			this.$refs.scroller.refresh()
		},50)
		//有问题-条数少了有问题
	}
  },
  computed:{
	productsArrs(){
		//截取数量小于10的显示出来
		//this.filterArrstock()
		//console.log(this.productsArr[this.currentType])
		return this.productsArr[this.currentType].list
	}
  },
  mounted(){
  },
  created(){
	//请求商品数据(4种类型的数据)
	this.getGoodsDatas('allSelect')
	this.getGoodsDatas('TodayChange')
	this.getGoodsDatas('StockShort')
	this.getGoodsDatas('StockMore')
  },
  activated(){
	//获取跳转过来的值
	//this.getGoodsDatas(this.currentType)
	//给后台传一个对象
	if(this.$route.params['info']){
		this.info=this.$route.params['info']['productName']
	}
	
  },
  watch:{
	info(newVal){
		console.log(newVal)
	},
  }
}
</script>

<style>
 .product-nav-bar{
	background:#fff;
 }

 .category{
	position:relative;
	height:100vh;
	background:#f1f1f1;
 }
 
 .product-scroller{
    position: relative;
	height:calc(100% - 94px);
    right: 0;
    left: 0;
	overflow:hidden;
  }
  .create-product{
	background:#f9f9f9;
	box-shadow: -1px 2px 5px #888888;
	position:fixed;
	bottom:80px;
	right:10px;
	border-radius:50px;
	padding:10px 16px;
	font-size:20px;
	border:1px solid #eaeaea
  }
  .product-btns{
	border-bottom:1px solid #eaeaea;
	background:#fff;
  }
  .nav2{
		position:fixed;
		width:100%;
		z-index:13;
  }
</style>
